nt_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n@mixin medium_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_medium_15;\n  color: $darkgrey_default;\n}\n\n@mixin small_eng {\n  font-family: $eng_font_regular;\n  font-size: $default_small_13;\n  color: $darkgrey_default;\n}\n\n// font-set-white\n@mixin big_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $sub_title_18;\n  color: $white;\n}\n\n@mixin medium_kor_white {\n  font-family: $kor_font_regular;\n  font-size: $default_medium_15;\n  color: $white;\n}\n\n.bold {\n  font-weight: bold;\n}\n\n.black {\n  color: black;\n}\n\n@import url('https://fonts.googleapis.com/css2?family=Rowdies&display=swap');\n","@import '@css/fonts.scss';\n@import '@css/useful.scss';\n\n.rotation--wrapper {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  @include medium_kor;\n}\n\n.rotation--selectDates {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin-bottom: 50px;\n}\n\n.rotation-selectDates-title {\n  margin-top: 30px;\n  margin-bottom: 30px;\n  text-align: center;\n}\n\n.rotation--viewSelectDates {\n  display: flex;\n  flex-direction: column;\n  justify-content: center;\n  align-items: center;\n  margin-bottom: 20px;\n}\n\n.rotation-viewSelectDates-title {\n  margin-top: 30px;\n  margin-bottom: 10px;\n}\n\n.rotation--selectDates-box {\n  //background-color: rgba(128, 128, 128, 0.4784313725);\n  outline: 1px solid gray;\n  width: 350px;\n  height: 50px;\n  padding: 5px;\n  margin-bottom: 3px;\n  border-radius: 4px;\n}\n\n.rotation--reset {\n  display: flex;\n  justify-content: flex-end;\n  width: 100%;\n  button {\n    @include medium_kor;\n    color: $pinkred;\n    font-weight: bold;\n    cursor: pointer;\n    font-size: 13px;\n    border: none;\n    border-radius: 10px;\n    transition: 0.5s ease;\n    display: flex;\n    align-items: center;\n    background: none;\n  }\n  button:hover {\n    text-decoration: underline;\n  }\n}\n\n.rotation--button {\n  display: flex;\n  flex-direction: row;\n  justify-content: center;\n  align-items: center;\n  flex-wrap: wrap;\n  button {\n    background-color: $lightgrey;\n    border: none;\n    border-radius: 10px;\n    width: 130px;\n    height: 65px;\n    margin: 0 5px;\n    font-size: 14px;\n    transition: 0.5s ease;\n    @include medium_kor;\n    &:hover {\n      box-shadow: 2px 2px 4px rgba(183, 183, 183, 0.5), 2px 2px 4px rgba(183, 183, 183, 0.5);\n      -webkit-transform: translateY(-5px);\n      transform: translateY(-5px);\n    }\n  }\n}\n\n.rotation--wrapper button {\n  margin-top: 5px;\n  margin-bottom: 5px;\n}\n\n\n.select-button {\n  width: 100px;\n  @include medium_kor;\n  outline: none;\n  border: none;\n  border-radius: 10px;\n  color: white;\n  font-weight: bold;\n  cursor: pointer;\n  padding-left: 1rem;\n  padding-right: 1rem;\n  height: 2.25rem;\n  font-size: 1rem;\n  background: #495057;\n  &:hover {\n    background: gray;\n  }\n  &:active {\n    background: #495057;\n  }\n}\n"],"sourceRoot":""}]);
